<template>
    <div>
        <div id="search">
            <span>&#xe69d;</span>
            <span>搜索附近的商家商品</span>
        </div>
        <div id="classify">
            <div class="left">
                <ul>
                    <li class="kind" v-for="(list,index) in lists" :key="list.guid" @click="showGoods(list.text,index)">
                        <router-link class="route" to="">{{list.text}}</router-link> 
                    </li>
                </ul>
            </div>
            <div class="right">
                <ul>
                    <li v-for="cls in clss" :key="cls.guid">
                        <p class="title">{{cls.title}}</p>
                        <ul class="dtls">
                            <li  class="dtl" v-for="children in cls.childrens" :key="children.guid">
                                <div><img :src="children.img" alt=""></div>
                                <p>{{children.text}}</p>
                            </li>
                        </ul>
                    </li>
                </ul>
            </div>
        </div>
    <footers></footers>
    </div>
</template>

<script>
import footers from "./footer.vue";
export default {
  data() {
    return {
      clss: [],
      lists: []
    };
  },
  created() {
    this.jQuery.get(this.apiURL + "/lists", { guid: this.guid }, result => {
      if (result.errcode == 0) {
        this.lists = result.data;
      }
    });
  },
  methods: {
    showGoods(text, index) {
      this.jQuery.get(this.apiURL + "/classify", { clstext: text }, result => {
        if (result.errcode == 0) {
          this.clss = result.data;
        }
      });
      this.jQuery(".kind")
        .eq(index)
        .addClass("border")
        .siblings()
        .removeClass("border");
    }
  },
  mounted(text){
     this.jQuery.get(this.apiURL + "/classify", { clstext: text }, result => {
      if (result.errcode == 0) {
        this.clss = result.data;
      }
    });
  },
  updated() {
    this.jQuery(".kind")
      .eq(0)
      .addClass("green");
  },
  components: {
    footers
  }
};
</script>

<style>
html,
body {
  width: 100%;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "iconfont";
}
#search ul {
  list-style: none;
}
#search {
  margin-top: 10px;
  border: 1px solid #e6e6e6;
  width: 92%;
  height: 30px;
  display: flex;
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 4%;
}
#search span:nth-child(1) {
  display: block;
  line-height: 30px;
  color: #757575;
  margin-left: 5px;
  font-size: 12px;
}
#search span:nth-child(2) {
  display: block;
  line-height: 30px;
  margin-left: 10px;
  color: #757575;
  font-size: 12px;
}
#classify {
  display: flex;
  width: 100%;
  height: 100%;
  overflow: hidden;
  padding-top: 10px;
  position: fixed;
  top: 35px;
}
#classify .left {
  width: 25%;
  background: #f8f8f8;
}
#classify .right {
  width: 75%;
  overflow: auto;
  margin-bottom: 80px;
}
#classify .kind {
  width: 100%;
  height: 40px;
  text-align: center;
  line-height: 40px;
  padding: 0 15px;
  background: #f8f8f8;
  font-size: 14px;
}
#classify .border {
  padding-left: 11px;
  color: #47b34f;
  background-color: #fff;
  border-left: 3px solid #47b34f;
  border-right-color: #fff;
}
#classify .title {
  font-size: 14px;
  padding-left: 10px;
  color: #7d7d7d;
  margin-top: 10px;
}
#classify .dtls {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  width: 100%;
}
#classify .dtl {
  width: 30%;
}
#classify .dtl div {
  height: 80px;
  width: 80%;
  margin: 0 auto;
}
#classify .dtl div img {
  height: 100%;
  width: 100%;
}
#classify .dtl p {
  text-align: center;
  font-size: 12px;
  color: #7d7d7d;
}
#classify .route:hover {
  text-decoration: none;
  color: #39ac69;
}
#classify .route {
  color: #555;
}
</style>



