<script>
//添加组件
import Swiper from "Swiper";
import "swiper/dist/css/swiper.css";
import index from "./index.vue";
import footers from "./footer.vue";
export default {
  data() {
    return {};
  },
  created() {
    //自动加载
    var $ = this.jQuery;
    $(window).scroll(function(event) {
      var top = $(this).scrollTop();
      var al = top / 300;
      if (al > 1) {
        return;
      }
      $(".search").css("background", "rgba(82,207,89," + (1 - al) + ")");
      $(".topnav").css("background", "rgba(82,207,89," + (0.4 + al) + ")");
      //设置顶部
      if (top < 10) {
        $(".topnav .right").css({ marginRight: "-30px", transition: "1s" });
      } else {
        $(".topnav .right").css({ marginRight: "0", transition: "1s" });
      }
    });
  },
  mounted() {
    //轮播图1
    var mySwiper1 = new Swiper(".swiper-container.swiperC1", {
      loop: true,
      autoplay: {
        disableOnInteraction: false
      },
      pagination: {
        el: ".swiper-pagination"
      }
    });
    //轮播图2
    var mySwiper2 = new Swiper(".swiper-container.swiperC2", {
      loop: false,
      slidesPerView: "auto",
      loopedSlides: 8
    });
  },
  components: {
    index,
    footers
  },
  methods:{
    
    back(){
      //搜索返回
     var $ = this.jQuery;
     $("#cover").slideUp()
    },
    showSearch(){
     var $ = this.jQuery;
     $("#cover").slideDown()
    }
  },
  
};
</script>
