<template>
    <div>
        <div id="order">
            <div class="shops" v-for="order in orders" :key="order.guid">
                <div class="title">
                    <span>{{order.title}}</span>
                    <span>已完成</span>
                </div>
                <div class="goods">
                    <div class="imgs">
                        <div v-for="img in order.imgs" :key="img.guid" class="pic">
                            <img :src="img.pic" alt="">
                        </div>
                    </div>
                    <span>
                        <p class="total">&yen;{{order.total}}</p>
                        <p class="num">共{{order.imgs.length}}件</p>
                        <p class="post">达达专送</p>
                    </span>
                </div>
                <div class="time">
                    {{order.date}}
                    {{order.time}}
                    <div>再次购买</div>
                </div>
            </div>
            <div class="line">
                <div></div>
                <span>已加载全部订单</span>
            </div>
        </div>
        <footers></footers>
    </div>
</template>

<script>
//添加footer组件
import footers from "./footer.vue";
export default {
  data() {
    return {
      orders: []
    };
  },
  //调用后台数据
  created() {
    this.jQuery.get(this.apiURL + "/order", result => {
      this.orders = result.data;
    });
  },
  components: {
    footers
  }
};
</script>

<style>
* {
  margin: 0;
  padding: 0;
  font-family: "iconfont";
  box-sizing: border-box;
}
html,
body {
  background: #f4f4f4;
}
#order .location {
  width: 70%;
  height: 30px;
  margin: 20px auto;
  background: #47b34f;
  border: 5px solid #d1e7d3;
  border-radius: 30px;
  color: white;
  text-align: center;
}
#order .shops {
  width: 100%;
  background: white;
  margin: 0 auto 30px auto;
  overflow: hidden;
}
#order .shops .title {
  width: 100%;
  background: #fcfcfc;
  height: 40px;
  padding-top: 10px;
  display: flex;
}
#order .shops .title span:nth-child(1) {
  width: 80%;
  padding-left: 15px;
}
#order .shops .title span:nth-child(2) {
  color: black;
}
#order .goods {
  width: 100%;
  display: flex;
  padding: 10px 10px;
  justify-content: flex-start;
  background: white;
}
#order .imgs {
  width: 80%;
  display: flex;
  justify-content: flex-start;
}
#order .shops .imgs div {
  width: 23%;
  height: 80px;
  padding: 10px;
}
#order .goods .imgs div img {
  width: 100%;
  height: 100%;
}
#order .goods .total {
  color: red;
  font-size: 20px;
}
#order .goods .num {
  font-size: 14px;
}
#order .goods .post {
  color: #54bfff;
  border: 1px solid #54bfff;
  font-size: 14px;
  border-radius: 3px;
  text-align: center;
}
#order .time {
  padding: 10px;
  border-top: 1px solid #dddddd;
  display: flex;
}
#order .time div {
  border: 1px solid #dddddd;
  padding: 0 5px;
  margin-left: 55%;
}
#order .line {
  margin: 50px auto 100px auto;
  width: 80%;
  text-align: center;
}
#order .line div {
  width: 100%;
  border-top: 1px solid #dddddd;
}
#order .line span {
  position: relative;
  top: -12px;
  background: #f4f4f4;
  color: grey;
  padding: 0 10px;
}
</style>



