<template>
  <div id="index">
    <ul>
        <div v-infinite-scroll="loadMore" infinite-scroll-disabled="busy" infinite-scroll-distance="10">
            <li  class="markets" v-for="(shop,index) in shops" :key="shop.guid">
                <div class="logo">
                  <img :src="shop.img" alt="">
                </div>
                <div class="shop">
                    <h3><router-link style="color:black" :to="'/shop/'+shop.guid+'/'+shop.title+'/'+shop.time+'/'+shop.img+'/'+shop.price">{{shop.title}}</router-link></h3>
                    <p class="time">达达专送-{{shop.time}}分钟</p>
                    <p class="star">{{shop.star}}
                      <span>|月售{{shop.num}}单</span>
                    </p>
                    <p class="dtls">
                      {{shop.price}}元起送,距离{{shop.distance}}km,基础运费{{shop.price}}元
                    </p>
                    <span class="down" v-show="shop.sales.length>2" @click="show(index)">﹀</span>
                    <span class="up" v-show="shop.sales.length>2" @click="hide(index)">︿</span>
                    <div class="sells">
                       <p class="sell" v-for="sale in shop.sales" :key="sale.guid">
                        <span class="act">{{sale.act}}</span>
                        <span class="sale">{{sale.text}}</span>
                      </p>
                    </div>                   
                </div>
               
            </li>
        </div>
    </ul>
  </div>
</template>
<script>
//滚动加载插件
import infiniteScroll from "vue-infinite-scroll";
export default {
  directives: { infiniteScroll },
  data() {
    return {
      busy: false,
      shops: []
    };
  },
  created() {
    //ajax请求数据
    this.jQuery.get(this.apiURL + "/shops", result => {
      this.shops = result.data;
      // console.log(this.shops)
    });
  },
  methods: {
    loadMore() {
      this.busy = true;
      this.jQuery.get(this.apiURL + "/shops", result => {
        this.shops = this.shops.concat(result.data);
        this.busy = false;
      });
    },
    show(index){
      this.jQuery("#index .sells").eq(index).css({"height":"auto"})
      this.jQuery("#index .down").eq(index).toggle()
      this.jQuery("#index .up").eq(index).toggle()
    },
    hide(index){
      this.jQuery("#index .sells").eq(index).css({"height":"65px"})
      this.jQuery("#index .up").eq(index).toggle()
      this.jQuery("#index .down").eq(index).toggle()
    }
  },
};
</script>
<style>
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
ul {
  list-style: none;
}
#index .markets {
  width: 100%;
  border-top: 1px solid #e7e9e4;
  display: flex;
  overflow: hidden;
  padding: 15px 10px;
}
#index .markets .logo {
  height: 65px;
  width: 65px;
  border: 1px solid #e7e9e4;
  border-radius: 5px;
}
#index .markets .logo img {
  height: 100%;
  width: 100%;
}
#index .markets .shop {
  margin-left: 15px;
  width: 80%;
  overflow: hidden;
}
#index .shop h3 {
  color: black;
}
#index .shop .time {
  color: #16a9ff;
  border: 1px solid #16a9ff;
  border-radius: 3px;
  font-size: 12px;
  width: 100px;
}
#index .star {
  color: #ffa14e;
  font-size: 12px;
}
#index .star span {
  color: #999999;
}
#index .dtls {
  font-size: 12px;
  color: #999999;
  border-bottom: 1px solid #e7e9e4;
  padding-bottom: 10px;
  width: 100%;
}
#index .sells{
  height: 65px;
  overflow: hidden;
}
#index .sell {
  display: flex;
  overflow: hidden;
}
#index .act {
  display: block;
  height: 15px;
  width: 30px;
  background: red;
  color: white;
  font-size: 12px;
  border-radius: 4px;
  margin-top: 10px;
  text-align: center;
  line-height: 15px;
}
#index .sale {
  font-size: 12px;
  margin-top: 10px;
  margin-left: 15px;
}
#index .more {
  height: 200px;
  overflow: hidden;
}
#index .down{
  float: right;
  margin-top: 10px;
  margin-right: 10px;
}
#index .up{
  float: right;
  margin-top: 10px;
  margin-right: 10px;
  display: none;
}
</style>




