<script>
import car from "./car.vue";
export default {
  //不能与data数据重名
  props: ["guid", "title", "time", "img", "price"],
  data() {
    return {
      ctgs: [],
      goods: [],
      ctgText: "",
      count: ""
    };
  },
  components: {
    car
  },
  created() {
    //初始化购物车
    if (!this.bus.cars[this.guid]) {
      this.$set(this.bus.cars, this.guid, []);
    }
    this.jQuery.get(this.apiURL + "/ctgs", { guid: this.guid }, result => {
      if (result.errcode == 0) {
        this.ctgs = result.data;
      }
    });
  },
  methods: {
    //后台调用商品数据
    showGoods(text) {
      this.jQuery.get(this.apiURL + "/goods", { ctgtext: text }, result => {
        if (result.errcode == 0) {
          this.goods = result.data;
          this.ctgText = result.ctgText;
          this.count = result.count;
        }
      });
    },
    //购物车添加
    addCar(gd) {
      var car = this.bus.cars[this.guid];
      for (var i = 0; i < car.length; i++) {
        if (car[i].guid == gd.guid) {
          if (car[i].num < gd.store) {
            car[i].num++;
            //重新双向绑定响应数据
            car.splice(i, 1, car[i]);
            this.$set(this.bus.carnums, gd.guid, car[i].num);
          }
          return;
        }
      }
      gd.num = 1; //商品的数量
      if (gd.num >= gd.store) {
        gd.num = gd.store;
      }
      car.push(gd);
      this.$set(this.bus.carnums, gd.guid, 1);
    },
    //购物车删减
    subCar(gd) {
      var car = this.bus.cars[this.guid];
      for (var i = 0; i < car.length; i++) {
        if (car[i].guid == gd.guid) {
          var num = --car[i].num;
          car.splice(i, 1, car[i]);
          if (num <= 0) {
            delete this.bus.carnums[gd.guid];
            car.splice(i, 1);
          }
          this.$set(this.bus.carnums, gd.guid, num);
          return;
        }
      }
    },
    //购物车详情
    showMenu() {
      this.jQuery(".menuBtn").toggle();
      this.jQuery(".arrow").toggle();
    },
    //显示二级菜单
    showSecond(index) {
      this.jQuery(".first ul")
        .eq(index)
        .slideToggle();
      this.jQuery(".first ul")
        .not(this.jQuery(".first ul").eq(index))
        .slideUp();
    },
    //显示优惠券
    toggle() {
      this.jQuery(".sales").slideToggle();
      this.jQuery("#shop").toggle();
      this.jQuery(".continue").css({ display: "block" });
      this.jQuery("#car").toggle();
      this.jQuery("#shopSearch").toggle();
    },
    //商店头部和内部的展开和收起
    cl() {
      this.jQuery(".a0v").css({ display: "none" });
      this.jQuery("#shopTop").css({ height: "108px" });
      this.jQuery(".shopAR").css({ display: "block" });
      this.jQuery(".xa").css({ display: "block" });
    },
    show() {
      this.jQuery(".shopAR").css({ display: "none" });
      this.jQuery(".xa").css({ display: "none" });
      this.jQuery("#shopTop").css({ position: "relative", height: "100%" });
      this.jQuery(".a0v").css({ display: "block" });
    },
    goodsMore() {
      if (this.jQuery(".a2v span").html() == "查看更多") {
        this.jQuery(".a2v span").html("点击收起");
        this.jQuery(".a2v i").css({ transform: "rotate(-180deg)" });
        this.jQuery(".af6").css({ height: "100%" });
      } else {
        this.jQuery(".a2v span").html("查看更多");
        this.jQuery(".af6").css({ height: "0" });
        this.jQuery(".a2v i").css({ transform: "rotate(0)" });
      }
    },
    //二级菜单展开和收起
    more(index) {
      this.jQuery(".lbox1")
        .not(this.jQuery(".lbox1").eq(index))
        .hide();
      this.jQuery(".lbox1")
        .eq(index)
        .toggle();
      this.jQuery(".lia")
        .eq(index)
        .css({ color: "#47b34f" });
      this.jQuery(".lia")
        .not(this.jQuery(".lia").eq(index))
        .css({ color: "#666" });
      if (
        this.jQuery(".lbox1")
          .eq(index)
          .css("display") == "none"
      ) {
        this.jQuery(".lia")
          .eq(index)
          .addClass("active");
        this.jQuery(".lia")
          .not(this.jQuery(".lia").eq(index))
          .removeClass("active");
      } else {
        this.jQuery(".lia")
          .eq(index)
          .removeClass("active");
      }
      this.jQuery(".lia")
        .not(this.jQuery(".lia").eq(index))
        .removeClass("active");
    },
    tog() {
      this.jQuery(".dhmore").toggle();
    }
  },
  mounted() {
    //后台获取商品信息
    this.jQuery.get(this.apiURL + "/goods", result => {
      if (result.errcode == 0) {
        this.goods = result.data;
        this.ctgText = result.ctgText;
        this.count = result.count;
      }
    });
  }
};
</script>


