<template>
    <div>
        <div id="details" v-for="detail in details" :key="detail.guid" >
            <div class="swiper-container">
                <div class="swiper-wrapper">
                    <div class="swiper-slide" v-for="img in detail.imgs" :key="img.guid"><img :src="img.pic" alt=""></div>
                </div>
                <div class="swiper-pagination"></div>
            </div>
            <p class="title">{{detail.title}}</p>
            <div>
                <div class="price">
                    <span>&yen;</span>
                    <span>{{detail.price}}</span>
                    <div class="addTo">
                        加入购物车
                    </div>
                </div>
            </div>
            <div class="info">
                <span>&#xe69e;&nbsp;{{detail.text}}</span>
                <span>&#xe62d;&nbsp;联系商家</span>
            </div>
            <div class="discuss">
                <p>商品评价
                    <span>(共{{detail.num}}人评价)</span>
                </p>
                <div class="line">
                    <div class="point"></div>
                </div>
               
            </div>
            <p class="up">好评100%</p>
            <div class="check">
                <p>查看图文详情</p>
                <div>></div>
            </div>
        </div>
        <footers></footers>
    </div>
</template>

<script>
import Swiper from "Swiper";
import "swiper/dist/css/swiper.css";
import footers from "./footer.vue"
export default {
  data() {
    return {
      details: [],
    };
  },
  created() {
    this.jQuery.get(this.apiURL + "/details", result => {
      this.details = result.data;
    });
  },
  updated() {
    var mySwiper = new Swiper(".swiper-container", {
      loop: true,
      pagination: {
        el: ".swiper-pagination",
        type: "fraction",
      }
    });
  },
  components:{
      footers
  }
};
</script>

<style>
*{
    margin: 0;
    padding: 0;
    font-family: "iconfont";
    box-sizing: border-box;
}
#details .swiper-container{
    width: 100%;
    height: 414px;
}
#details .swiper-slide img{
    height: 100%;
    width: 100%;
}
#details .swiper-pagination{
    margin-left: 85%;
    color: white;
    font-size: 18px;
    background: rgba(0, 0, 0, .4);
    width: 55px;
    height: 25px;
    border-radius: 25px;
}
#details .title{
    margin-top: 10px;
    font-size: 18px;
    text-align: center;
}
#details .price{
    padding: 0 20px;
    overflow: hidden;
    border-bottom: 10px solid #F4F4F4;
}
#details .price span{
    float: left;
    font-size: 30px;
    color: red;
}
#details .addTo{
    height: 35px;
    width: 80px;
    background: #47B34F;
    font-size: 14px;
    border-radius: 3px;
    text-align: center;
    line-height: 35px;
    color: white;
    float: right;
}
#details .info{
    display: flex;
    font-size: 15px;
    overflow: hidden;
    padding: 15px 15px;
}
#details .info span:nth-child(1){
    width: 60%;
}
#details .info span:nth-child(2){
    width: 40%;
    text-align: right;
    padding-right: 10px;
    border-left: 1px solid #BEBEBE;
}
#details .line{
    border-top: 2px solid #47b34f;
    width: 75%;
}
#details .discuss{
    overflow: hidden;
    width: 100%;
    padding-left: 15px;
    font-size: 14px;
}
#details .point{
    border: 2px solid #47b34f;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    float: right;
    position: relative;
    top: -5px;
    left: 6px;
}
#details .up{
    float: right;
    font-size: 14px;
    color: #47b34f;
    margin-top: -20px;
    padding-right: 5px;
}
#details .check{
    display: flex;
    background: #F4F4F4;
    width: 100%;
    height: 250px;
    padding-top: 40px;
}
#details .check div{
    height: 18px;
    width: 18px;
    border-radius: 50%;
    background: #47b34f;
    color: white;
    line-height: 15px;
    text-align: center;
    margin-top: 2px;
    margin-left: 5px;
}
#details .check p{
    width: 60%;
    text-align: right;
}
</style>



