<template>
    <div>
        <div id="login">
          <div class="topbar">
            <span>
              <router-link to="/mine" style="color:grey">〈</router-link>
            </span>
            <span>京东登录</span>
          </div>
          <div class="select">
            <div @click="change1">
              <span class="border">账号密码登录</span>
            </div>
            <div @click="change2">
              <span>快速注册</span>
            </div>
          </div>
          <div v-show="isShow" class="memberLogin">
            <p>
              账号:
              <input type="text" placeholder="用户名/邮箱/已验证手机" name="name1">
            </p>
            <p>
              密码:
              <input type="password" placeholder="请输入密码" name="password">
            </p>
             <div v-show="isShow" class="join">
              登录
            </div>
            <div v-show="isShow" class="quick">
              一键登录
            </div>
          </div>
          <div v-show="!isShow" class="register">
            <el-form id="regFrm" :rules="rules" label-position="left" ref="form"
                :model="form" label-width="80px" action="/">
                <el-form-item label="用户名" prop="username">
                    <el-input v-model="form.username" name="username"></el-input>
                </el-form-item>

                <el-form-item label="密码" prop="password">
                    <el-input v-model="form.password" name="password" type="password"></el-input>
                </el-form-item>

                <el-form-item label="确认密码" prop="password2">
                    <el-input v-model="form.password2" type="password"></el-input>
                </el-form-item>

                <el-form-item label="手机号码" prop="phone">
                    <el-input v-model="form.phone" name="phone"></el-input>
                </el-form-item>

                <el-form-item label="邮箱" prop="email">
                    <el-input v-model="form.email" name="email"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button @click="regist()" type="primary" class="btn">立即注册</el-button>
                </el-form-item>
            </el-form>
          </div>
          <div class="forget">
            <span>
              忘记密码
            </span>
            <span>
              手机快速注册
            </span>
          </div>
          <div class="other">
            <span>其他登录方式</span>
            <div>&#xe628;</div>
            <p>登录即代表您已同意京东隐私政策</p>
          </div>
            <div class="cover"></div>
            <div class="import">
                <h3>注册协议及隐私政策</h3>
                <p>在您注册成为京东用户的过程中，您需要完成我们的注册流程并通过点击同意的形式在线签署以下协议，</p>
                <p>《京东用户注册协议》</p>
                <p>《用户隐私政策》</p>
                <p>《订单共享与安全》</p>
                <p>点击同意即表示您已阅读并同意<span>《京东用户注册协议》</span>与<span>《京东隐私政策》</span>并将您的订单信息共享给为完成此订单所必须的第三方合作方。关于<span>《订单共享与安全》</span></p>
                <div class="agreement">
                    <div><router-link to="/mine" style="color:black">不同意</router-link></div>
                    <div @click="agree">同意</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  data() {
    var _this = this;
    //验证两次密码是否一致
    var chkPwd = function(value, callback) {
      if (value.trim() == "") {
        callback(new Error("请输入确认密码"));
      } else if (value == _this.form.password) {
        callback();
      } else {
        callback(new Error("两次输入密码不一致"));
      }
    };
    return {
      isShow: true,
      isCheckedAll: false,
      form: {
        username: "",
        password: "",
        password2: "",
        email: "",
        phone: "",
        sex: "1",
      },
      rules: {
        username: [
          {
            validator: function(rule, value, callback) {
              var usernamereg = /^[a-z]\w{2,17}$/;
              if (value.trim() == "") {
                callback(new Error("请输入用户名"));
              } else if (!usernamereg.test(value)) {
                callback(new Error("请输入2-18个字符，以小写字母开始"));
              } else {
                callback();
              }
            },
            trigger: "blur"
          }
        ],
        password: [
          {
            validator: function(rule, value, callback) {
              var len = value.length;
              if (value.trim() == "") {
                callback(new Error("请输入密码"));
              } else if (len < 6 || len > 18) {
                callback(new Error("密码长度6-18个字符"));
              } else {
                _this.$refs.form.validateField("password2");
                callback();
              }
            }
          }
        ],
        password2: [
          {
            validator: function(rule, value, callback) {
              chkPwd(value, callback);
            }
          }
        ],
        email: [
          {
            validator: function(rule, value, callback) {
              var emailreg = /^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$/;
              if (value.trim() == "") {
                callback(new Error("请输入邮箱"));
              } else if (!emailreg.test(value)) {
                callback(new Error("邮箱格式不合法"));
              } else {
                callback();
              }
            },
            trigger: "blur"
          }
        ],
        phone: [
          {
            validator: function(rule, value, callback) {
              var phonereg = /^1([358][0-9]|4[579]|66|7[0135678]|9[89])[0-9]{8}$/;
              if (value.trim() == "") {
                callback(new Error("请输入手机号"));
              } else if (!phonereg.test(value)) {
                callback(new Error("无效手机号码"));
              } else {
                callback();
              }
            },
            trigger: "blur"
          }
        ]
      }
    };
  },
  methods: {
    agree() {
      this.jQuery(".cover").fadeOut();
      this.jQuery(".import").fadeOut();
    },
    //点击切换注册和登录
    change1() {
      this.jQuery(".select div:nth-child(1) span").addClass("border");
      this.jQuery(".select div:nth-child(2) span").removeClass("border");
      this.isShow = true;
    },
    change2() {
      this.jQuery(".select div:nth-child(2) span").addClass("border");
      this.jQuery(".select div:nth-child(1) span").removeClass("border");
      this.isShow = false;
    },
    regist: function() {
      this.$refs.form.validate(function(valid) {
        if (valid) {
          //验证全部通过提交数据
          regFrm.submit();
        }
      });
    },
    checkedAll () {
      this.isCheckedAll = !this.isCheckedAll
      if (this.isCheckedAll) {
        // 全选时
        this.fruitIds = []
        this.fruits.forEach(function (fruit) {
          this.fruitIds.push(fruit.fruitId)
        }, this)
      } else {
        this.fruitIds = []
      }
    },
  }
};
</script>

<style>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "iconfont";
}
#login .cover {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.6);
}
#login .import {
  width: 100%;
  position: fixed;
  left: 0;
  bottom: 0;
  background: white;
}
#login .border {
  border-bottom: 2px solid red;
}
#login .import h3 {
  text-align: center;
  border-bottom: 1px solid #d7d7d7;
  padding: 10px 10px;
}
#login .import p {
  font-size: 14px;
  padding: 10px 10px 0 10px;
}
#login .import p span {
  color: red;
  text-decoration-line: underline;
  font-weight: bold;
}
#login .import .agreement {
  display: flex;
  justify-content: space-around;
  padding-bottom: 10px;
}
#login .agreement div {
  width: 40%;
  height: 35px;
  text-align: center;
  border: 1px solid #d7d7d7;
  line-height: 35px;
  font-weight: bold;
  border-radius: 5px;
}
#login .agreement div:nth-child(2) {
  background: #e93b3d;
  color: white;
}
#login .topbar {
  height: 45px;
  width: 100%;
  background: #f9f9f9;
  border-bottom: 1px solid #bbbbbb;
  line-height: 45px;
  display: flex;
  color: #616161;
}
#login .topbar span:nth-child(1) {
  width: 40%;
  font-size: 20px;
}
#login .select {
  height: 45px;
  width: 100%;
  line-height: 45px;
  border-bottom: 1px solid #bbbbbb;
  display: flex;
  font-size: 14px;
}
#login .select div {
  width: 50%;
  text-align: center;
}
#login .select div span {
  padding: 0 10px 13px 10px;
}
#login .memberLogin {
  padding: 5px 25px;
  font-size: 14px;
}
#login .memberLogin p {
  width: 100%;
  height: 45px;
  border-bottom: 1px solid #bbbbbb;
  line-height: 45px;
}
#login .memberLogin input {
  border: none;
  outline: none;
  height: 40px;
  width: 85%;
  margin-left: 10px;
}
#login input::-webkit-input-placeholder {
  color: #cccccc;
}
#login .register {
  padding: 10px;
  font-size: 14px;
}
#login .register p {
  width: 100%;
  height: 45px;
  border-bottom: 1px solid #bbbbbb;
  line-height: 45px;
}
#login .register input {
  width: 88%;
  outline: none;
  height: 40px;
  margin-left: 10px;
}
#login .join {
  width: 90%;
  margin: 0 auto;
  background: #eeeeee;
  color: #848689;
  height: 45px;
  text-align: center;
  line-height: 45px;
  margin-top: 20px;
}
#login .quick {
  width: 90%;
  margin: 15px auto;
  color: #f23030;
  border: 1px solid #f23030;
  height: 45px;
  text-align: center;
  line-height: 45px;
}
#login .forget {
  display: flex;
  font-size: 12px;
}
#login .forget span:nth-child(1) {
  width: 75%;
  padding-left: 10%;
}
#login .other {
  width: 90%;
  margin: 90px auto auto auto;
  border-top: 1px solid #d7d7d7;
  position: relative;
}
#login .other span {
  margin: 0 auto;
  width: 120px;
  height: 20px;
  text-align: center;
  display: block;
  font-size: 14px;
  position: absolute;
  left: 50%;
  top: 0;
  margin-top: -10px;
  margin-left: -60px;
  background: white;
  color: #bfbfbf;
}
#login .other div {
  width: 100%;
  text-align: center;
  margin-top: 30px;
  font-size: 30px;
  color: #81d741;
}
#login .other p {
  width: 100%;
  margin-top: 10px;
  text-align: center;
  font-size: 12px;
}
#login .btn {
  width: 90%;
}
</style>



