<template>
    <div id="car">
        <div class="payment">
          <div class="logo" @click="showDtl">
            <span>&#xe63f;</span>
            <div v-show="totalNum>0">{{totalNum}}</div>
          </div>
          <table>
            <div class="empty" @click="emptyCar">&#xe69e;清空购物车</div>
            <input type="checkbox" @click='checkedAll()' style="margin:5px auto auto 10px" id="all" :checked="isChecked">全选
            <tr v-for="car in bus.cars[shopid]" :key="car.guid">
                <td>
                    <input type="checkbox" v-model="checked" :value="car.guid" @click="chked">
                </td>
                <td><div class="pic"><img :src="car.img" alt=""></div></td>
                <td>{{car.text}}</td>
                <td>{{car.price}}</td>
                <td class="numbers">
                    <span class="button" @click="subCar(car)">-</span>
                    {{car.num}}
                     <span class="button" @click="addCar(car)">+</span>
                </td>
                <td>&yen;{{Math.round(car.num*car.price)}}</td>
            </tr>
          </table>
          <div class="total">&yen;<b>{{total}}</b></div>
          <div class="pay" v-show="totalNum>0" style="background:#47B34F">
            <router-link to="/shopcar" style="color:white">
              去结算
            </router-link>
          </div>
          <div class="pay" v-show="totalNum<=0">去结算</div>
        </div>
        
    </div>
</template>

<script>
export default {
  props: ["shopid"],
  data() {
    return {
      checked: [],
      isChecked: false
    };
  },
  computed: {
    total() {
      //总价
      var sum = 0;
      var shopid = this.bus.cars[this.shopid];
      for (var i = 0; i < shopid.length; i++) {
        if (this.checked.indexOf(shopid[i].guid) == -1) {
          continue;
        }
        sum += Math.round(shopid[i].price * shopid[i].num);
      }
      return sum;
    },
    totalNum() {
      //总个数
      var sum = 0;
      var shopid = this.bus.cars[this.shopid];
      for (var i = 0; i < shopid.length; i++) {
        sum += shopid[i].num;
      }
      return sum;
    }
  },
  methods: {
    //全选
    checkedAll() {
      var car = this.bus.cars[this.shopid];
      for (var i = 0; i < car.length; i++) {
        if (this.checked.indexOf(car[i].guid) == -1) {
          this.checked.push(car[i].guid);
          this.isChecked = true;
        } else {
          this.checked = [];
          this.isChecked = false;
        }
      }
    },
    //单选
    chked() {
      var car = this.bus.cars[this.shopid];
      if (this.checked.length != car.length) {
        this.isChecked = false
      } else{
         this.isChecked = true
      }
    },
    emptyCar() {
      this.$set(this.bus.cars, this.shopid, []);
      this.$set(this.bus, "carnums", {});
      this.jQuery("table").css({ display: "none" });
      this.jQuery(".cover").css({ display: "none" });
    },
    addCar(gd) {
      var car = this.bus.cars[this.shopid];
      for (var i = 0; i < car.length; i++) {
        if (car[i].guid == gd.guid) {
          if (car[i].num < gd.store) {
            car[i].num++;
            //重新双向绑定响应数据
            car.splice(i, 1, car[i]);
            this.$set(this.bus.carnums, gd.guid, car[i].num);
          }
          return;
        }
      }
      gd.num = 1; //商品的数量
      if (gd.num >= gd.store) {
        gd.num = gd.store;
      }
      car.push(gd);
      this.$set(this.bus.carnums, gd.guid, 1);
    },
    subCar(gd) {
      var car = this.bus.cars[this.shopid];
      for (var i = 0; i < car.length; i++) {
        if (car[i].guid == gd.guid) {
          var num = --car[i].num;
          car.splice(i, 1, car[i]);
          if (num < 0) {
            delete this.bus.carnums[gd.guid];
            car.splice(i, 1);
          }
          this.$set(this.bus.carnums, gd.guid, num);
          return;
        }
      }
    },
    //购物车弹出
    showDtl() {
      if(this.bus.cars[this.shopid].length>0){
        this.jQuery("table").slideToggle();
        this.jQuery(".logo").css({ top: "-20px" });
        this.jQuery(".cover").fadeToggle();
        }
    }
  }
};
</script>


<style>
* {
  margin: 0;
  padding: 0;
  font-family: "iconfont";
}
#car {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background: white;
  border: 1px solid #bebebe;
  z-index: 10;
}
#car .logo {
  height: 42px;
  width: 42px;
  border-radius: 50%;
  background: #bebebe;
  line-height: 42px;
  text-align: center;
  margin-top: 2px;
  color: white;
  font-size: 20px;
  float: left;
  position: absolute;
  top: -20px;
  left: 0;
  background: #47B34F;
}
#car .logo div {
  height: 17px;
  width: 17px;
  border-radius: 50%;
  background: #ff5d5c;
  position: absolute;
  top: -2px;
  right: -5px;
  color: white;
  font-size: 12px;
  text-align: center;
  line-height: 17px;
}
#car .pay {
  height: 50px;
  width: 150px;
  color: white;
  font-size: 18px;
  background: #bebebe;
  line-height: 50px;
  text-align: center;
  float: right;
}
#car > p {
  margin-left: 15px;
  color: #bebebe;
  width: 150px;
  font-size: 16px;
  float: left;
  line-height: 50px;
}
#car .total {
  width: 100px;
  float: left;
  margin-top: 10px;
  font-size: 20px;
  color: red;
  padding-left: 50px;
}
#car table {
  width: 100%;
  display: none;
}
#car .numbers {
  display: flex;
  padding-top: 15px;
}
#car td {
  padding: 0 10px;
}
#car .empty {
  text-align: right;
  color: #666;
  font-size: 14px;
}
#car .pic {
  height: 55px;
  width: 55px;
}
#car .pic img {
  height: 100%;
  width: 100%;
}
#car .button {
  height: 20px;
  width: 20px;
  border-radius: 50%;
  border: 1px solid #e5e5e5;
  text-align: center;
  line-height: 15px;
  color: #47b34f;
  font-size: 20px;
  font-weight: bold;
}
#car input[type="checkbox"] {
  -webkit-appearance: none;
}
#car input[type="checkbox"] {
  -webkit-appearance: none;
  background: #fff url("/static/ck.png") center top;
  background-size: 100%;
  height: 20px;
  vertical-align: middle;
  width: 20px;
  outline: none;
}
#car input[type="checkbox"]:checked {
  background-position: 0 -200px;
  outline: none;
}
</style>



