<template>
    <div>
        <div id="shopcar">
            <div class="location">
                &#xe662;当前位置
            </div>
            <div class="shops" v-for="order in orders" :key="order.guid">
                <div class="title">
                    <span>{{order.title}}</span>
                    <span>〉</span>
                </div>
                <div class="goods">
                    <div v-for="img in order.imgs" :key="img.guid">
                        <img :src="img.pic" alt="">
                        <p>&yen;{{img.price}}</p>
                    </div>
                </div>
            </div>
        </div>
        <footers></footers>
    </div>
</template>

<script>
import footers from "./footer.vue";
export default {
  data() {
    return {
      orders: []
    };
  },
  created() {
    this.jQuery.get(this.apiURL + "/order", result => {
      this.orders = result.data;
    });
  },
  components: {
    footers
  }
};
</script>

<style>
* {
  margin: 0;
  padding: 0;
  font-family: "iconfont";
  box-sizing: border-box;
}
html,
body {
  background: #f4f4f4;
}
#shopcar {
  margin-bottom: 80px;
}
#shopcar .location {
  width: 70%;
  height: 30px;
  margin: 20px auto;
  background: #47b34f;
  border: 5px solid #d1e7d3;
  border-radius: 30px;
  color: white;
  text-align: center;
}
#shopcar .shops {
  width: 90%;
  background: white;
  margin: 15px auto;
  overflow: hidden;
}
#shopcar .shops .title {
  width: 100%;
  background: #fcfcfc;
  height: 40px;
  padding-top: 10px;
  display: flex;
}
#shopcar .shops .title span:nth-child(1) {
  width: 95%;
  padding-left: 15px;
}
#shopcar .shops .title span:nth-child(2) {
  color: #cacaca;
}
#shopcar .shops .goods {
  display: flex;
  padding: 10px 10px;
  justify-content: flex-start;
}
#shopcar .shops .goods div {
  width: 20%;
  height: 80px;
  padding: 10px;
}
#shopcar .shops .goods div img {
  width: 100%;
  height: 100%;
}
#shopcar .shops .goods p {
  font-size: 12px;
  text-align: center;
}
</style>



