import Vue from 'vue'
import router from './router.js'
import config from "./config.js"
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
Vue.use(ElementUI);
Vue.use(config)
new Vue({
  el: '#app',
  router,
  template: `<div><router-view/></div>`
})



